// Mock del servicio de autenticaci√≥n para desarrollo
export interface LoginCredentials {
  email: string;
  password: string;
  rememberMe: boolean;
}

export interface AuthResponse {
  success: boolean;
  token?: string;
  user?: {
    id: string;
    email: string;
    name: string;
  };
  message?: string;
}

// Usuarios de prueba
const mockUsers = [
  {
    id: '1',
    email: 'admin@quienmas.com',
    password: 'admin123',
    name: 'Administrador'
  },
  {
    id: '2',
    email: 'usuario@quienmas.com',
    password: 'usuario123',
    name: 'Usuario Prueba'
  },
  {
    id: '3',
    email: 'test@quienmas.com',
    password: 'test123',
    name: 'Usuario Test'
  }
];

// Funci√≥n para simular delay de red
const simulateNetworkDelay = (ms: number = 1000) => {
  return new Promise(resolve => setTimeout(resolve, ms));
};

// Funci√≥n para generar token JWT mock
const generateMockToken = (userId: string) => {
  const header = btoa(JSON.stringify({ alg: 'HS256', typ: 'JWT' }));
  const payload = btoa(JSON.stringify({ 
    userId, 
    iat: Date.now(), 
    exp: Date.now() + (24 * 60 * 60 * 1000) // 24 horas
  }));
  const signature = btoa('mock-signature');
  return `${header}.${payload}.${signature}`;
};

export class MockAuthService {
  static async login(credentials: LoginCredentials): Promise<AuthResponse> {
    try {
      // Simular delay de red
      await simulateNetworkDelay(1500);

      // Buscar usuario
      const user = mockUsers.find(u => 
        u.email === credentials.email && u.password === credentials.password
      );

      if (!user) {
        return {
          success: false,
          message: 'Credenciales inv√°lidas. Verifica tu email y contrase√±a.'
        };
      }

      // Generar token mock
      const token = generateMockToken(user.id);

      // Simular √©xito
      return {
        success: true,
        token,
        user: {
          id: user.id,
          email: user.email,
          name: user.name
        }
      };

    } catch (error) {
      console.error('Error en MockAuthService.login:', error);
      return {
        success: false,
        message: 'Error interno del servidor. Intenta nuevamente.'
      };
    }
  }

  static async logout(): Promise<void> {
    try {
      // Simular delay de red
      await simulateNetworkDelay(500);
      console.log('Logout exitoso (mock)');
    } catch (error) {
      console.error('Error en logout:', error);
    }
  }

  static getToken(): string | null {
    return localStorage.getItem('authToken');
  }

  static isAuthenticated(): boolean {
    return !!this.getToken();
  }

  // M√©todo para obtener informaci√≥n del usuario actual
  static getCurrentUser() {
    const token = this.getToken();
    if (!token) return null;

    try {
      const payload = JSON.parse(atob(token.split('.')[1]));
      const user = mockUsers.find(u => u.id === payload.userId);
      return user ? {
        id: user.id,
        email: user.email,
        name: user.name
      } : null;
    } catch (error) {
      console.error('Error al decodificar token:', error);
      return null;
    }
  }
}
