'use client';

import React, { useEffect, useState } from 'react';
import { MockAuthService } from '../../services/mockAuthService';

export default function DashboardPage() {
  const [user, setUser] = useState<any>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Verificar si el usuario est√° autenticado
    const currentUser = MockAuthService.getCurrentUser();
    if (currentUser) {
      setUser(currentUser);
    } else {
      // Si no hay usuario, redirigir al login
      window.location.href = '/login';
    }
    setIsLoading(false);
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('authToken');
    localStorage.removeItem('rememberMe');
    window.location.href = '/';
  };

  if (isLoading) {
    return (
      <div className="min-h-screen bg-yellow flex items-center justify-center">
        <div className="text-2xl text-dark-purple">Cargando...</div>
      </div>
    );
  }

  if (!user) {
    return null;
  }

  return (
    <div className="min-h-screen bg-yellow flex">
      {/* üß≠ Men√∫ lateral */}
      <aside className="hidden md:flex flex-col w-64 bg-dark-purple text-white rounded-r-3xl shadow-2xl p-6">
        <h2 className="text-2xl font-bold mb-8">Panel de Usuario</h2>
        <nav className="space-y-4">
          <button className="w-full text-left hover:bg-white hover:text-dark-purple rounded-lg px-4 py-2 transition-colors">
            üè† Inicio
          </button>
          <button className="w-full text-left hover:bg-white hover:text-dark-purple rounded-lg px-4 py-2 transition-colors">
            üë§ Perfil
          </button>
          <button className="w-full text-left hover:bg-white hover:text-dark-purple rounded-lg px-4 py-2 transition-colors">
            üìä Actividad
          </button>
          <button className="w-full text-left hover:bg-white hover:text-dark-purple rounded-lg px-4 py-2 transition-colors">
            ‚öôÔ∏è Configuraci√≥n
          </button>
          <button
            onClick={handleLogout}
            className="mt-10 bg-orange-red text-white px-4 py-2 rounded-lg font-semibold hover:bg-red-600 transition-colors"
          >
            Cerrar Sesi√≥n
          </button>
        </nav>
      </aside>

      {/* üìÑ Contenido principal */}
      <main className="flex-1 p-8">
        <div className="max-w-6xl mx-auto">
          <div className="bg-white rounded-3xl shadow-2xl p-8">
            {/* üëã Bienvenida */}
            <div className="mb-10 text-center">
              <h1 className="text-5xl font-bold text-dark-purple mb-4">
                ¬°Bienvenido, {user.name}!
              </h1>
              <p className="text-lg text-gray-700">
                Nos alegra verte de nuevo üòä. Aqu√≠ puedes revisar tu informaci√≥n, tus actividades y estad√≠sticas generales.
              </p>
            </div>

            {/* üß† Informaci√≥n del usuario y autenticaci√≥n */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-10">
              <div className="bg-yellow bg-opacity-20 p-6 rounded-xl">
                <h2 className="text-2xl font-bold text-dark-purple mb-4">
                  Informaci√≥n del Usuario
                </h2>
                <div className="space-y-2 text-gray-800">
                  <p><strong>ID:</strong> {user.id}</p>
                  <p><strong>Email:</strong> {user.email}</p>
                  <p><strong>Nombre:</strong> {user.name}</p>
                  <p><strong>Rol:</strong> {user.role || 'Usuario registrado'}</p>
                  <p><strong>√öltimo acceso:</strong> {new Date().toLocaleString()}</p>
                </div>
              </div>

              <div className="bg-blue bg-opacity-20 p-6 rounded-xl">
                <h2 className="text-2xl font-bold text-dark-purple mb-4">
                  Estado de Autenticaci√≥n
                </h2>
                <div className="space-y-2 text-gray-800">
                  <p><strong>Autenticado:</strong> ‚úÖ S√≠</p>
                  <p><strong>Token:</strong> {MockAuthService.getToken() ? '‚úÖ Presente' : '‚ùå No encontrado'}</p>
                  <p><strong>Recordarme:</strong> {localStorage.getItem('rememberMe') ? '‚úÖ Activado' : '‚ùå Desactivado'}</p>
                  <p><strong>Sesi√≥n iniciada:</strong> {localStorage.getItem('isLoggedIn') ? '‚úÖ Activa' : 'üî¥ Inactiva'}</p>
                </div>
              </div>
            </div>

            {/* üìÖ Actividad reciente */}
            <div className="bg-purple-100 p-6 rounded-xl mb-10">
              <h2 className="text-2xl font-bold text-dark-purple mb-4">Actividad Reciente</h2>
              <ul className="space-y-2 text-gray-800">
                <li>üì© Inicio de sesi√≥n exitoso ‚Äî {new Date().toLocaleTimeString()}</li>
                <li>üß≠ Naveg√≥ al panel principal</li>
                <li>üìÅ Consult√≥ su perfil de usuario</li>
                <li>‚öôÔ∏è Revis√≥ configuraci√≥n de cuenta</li>
              </ul>
            </div>

            {/* üìà Estad√≠sticas */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <div className="bg-green-100 p-6 rounded-xl text-center">
                <h3 className="text-3xl font-bold text-green-700">12</h3>
                <p className="text-gray-700 mt-2">Sesiones activas</p>
              </div>
              <div className="bg-orange-100 p-6 rounded-xl text-center">
                <h3 className="text-3xl font-bold text-orange-600">4</h3>
                <p className="text-gray-700 mt-2">Notificaciones nuevas</p>
              </div>
              <div className="bg-blue-100 p-6 rounded-xl text-center">
                <h3 className="text-3xl font-bold text-blue-600">98%</h3>
                <p className="text-gray-700 mt-2">Nivel de actividad</p>
              </div>
            </div>

            {/* üü¢ Mensaje final */}
            <div className="mt-10 p-6 bg-green-100 rounded-xl text-center">
              <h2 className="text-2xl font-bold text-green-800 mb-4">
                üéâ ¬°Login Exitoso!
              </h2>
              <p className="text-green-700">
                Has iniciado sesi√≥n correctamente usando el sistema mock.  
                Tu informaci√≥n se encuentra sincronizada y puedes continuar navegando en el panel.
              </p>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
